import React from 'react';
import logo from './dog.png';
import { usePersistentCanvas, getTriangleCoords, fill } from './hooks'
import './App.css'
import { TwitterPicker } from 'react-color';
var FileSaver = require('file-saver');

function App() {
var [locations, setLocations,canvasRef] = usePersistentCanvas([])
var currColor = "#0693E3"
var stage = 0;
var h = 300;

function inside(point, vs) {
    // ray-casting algorithm based on
    // http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html

    var x = point[0], y = point[1];
    var inside = false;
    for (var i = 0, j = vs.length - 1; i < vs.length; j = i++) {
        var xi = vs[i][0], yi = vs[i][1];
        var xj = vs[j][0], yj = vs[j][1];
        var intersect = ((yi > y) !== (yj > y))
            && (x < (xj - xi) * (y - yi) / (yj - yi) + xi);
        if (intersect) inside = !inside;
    }
    return inside;
};

function handleCanvasClick(e) {
  //var xgrid = Math.floor(e.clientX / 50)*50;
  //var ygrid = Math.floor(e.clientY / 50)*50;
  //console.log(h);
  if (stage === 0) {
    //console.log(window.scrollY)
    setLocations([...locations, { x: e.clientX, y: e.clientY + window.scrollY-h}])
  }
}

function handleFill(e) {
  if (canvasRef === null || stage === 0) {
    return;
  }
  const ctx = canvasRef.current.getContext('2d')

  var coordinates = getTriangleCoords(locations);
  for (var i=0;i<coordinates.length;i++) {
    if (inside([e.clientX,e.clientY + window.scrollY-h], coordinates[i])) {
      fill(ctx,coordinates[i],currColor)
    }
  }
}

function handleNext(e) {
  if (stage === 0) {
    canvasRef.current.removeEventListener('click', handleCanvasClick.bind(this));
    canvasRef.current.addEventListener("contextmenu", handleFill.bind(this));
    stage = 1;
  } else if (stage === 1) {
    var node = document.getElementById('cpage');
    canvasRef.current.toBlob(function(blob) {
        FileSaver.saveAs(blob, "flagapp.png");
    });
  }
}

function handleClear() {
  setLocations([])
  stage = 0;
}

function handleUndo() {
  if (stage === 0) {
    setLocations(locations.slice(0, -1))
  }
}

var handleChangeComplete = (color) => {
  currColor = color.hex;
};

return (
  <div className="App">
    <header className="App-header">
      <img src={logo} className="App-logo" alt="logo"/>
      <p>
        <i><b>flag</b>app</i>
      </p>
      <div className="colorPick">
      <TwitterPicker
        color={ "#eee" }
        onChangeComplete={ handleChangeComplete }
        triangle={ "hide" }
      />
      </div>
    </header>
    <div className="controls">
      <button onClick={handleClear}>Clear</button>
      <button onClick={handleUndo}>Undo</button>
      <button onClick={handleNext}>Next Step</button>
    </div>
    <canvas
      id="cpage"
      ref={canvasRef}
      width={window.innerWidth}
      height={window.innerHeight}
      onClick={handleCanvasClick}
    />
  </div>
)
}
export default App;
