import { useState, useEffect, useRef } from 'react'
import Delaunator from 'delaunator'

export function draw(ctx, location) {
  ctx.fillStyle = 'black'
  ctx.save()
  ctx.fillRect(location.x-3,location.y-3,6,6);
  ctx.restore()
}

export function fill(ctx, triangle,currColor) {
  var path=new Path2D();
  path.moveTo(triangle[0][0],triangle[0][1])
  path.lineTo(triangle[1][0],triangle[1][1])
  path.lineTo(triangle[2][0],triangle[2][1])
  ctx.stroke()
  ctx.fillStyle = currColor
  ctx.fill(path)
}

export function usePersistentState(init) {
  const [locations, setLocations] = useState(
    JSON.parse(localStorage.getItem('draw-app')) || init
  )

  useEffect(() => {
    localStorage.setItem('draw-app', JSON.stringify(locations))
  })

  return [locations, setLocations]
}

export function getTriangleCoords(locations) {
  var points = [[0,0],[window.innerWidth,0],[0,window.innerHeight],[window.innerWidth,window.innerHeight]];
  for (var items in locations){
      points.push( [locations[items].x, locations[items].y] );
  }
  const del = Delaunator.from(points);
  const triangles = del.triangles;
  var coordinates = [];
  for (let i = 0; i < triangles.length; i += 3) {
      // const p1 = triangles[i];
      // const p2 = triangles[i+1];
      // const p3 = triangles[i+2];

      var coords = [
          points[triangles[i]],
          points[triangles[i + 1]],
          points[triangles[i + 2]]
        ];

      coordinates.push(coords);
  }

  return coordinates;
}

export function usePersistentCanvas() {
  const [locations, setLocations] = usePersistentState([])
  const canvasRef = useRef(null)

  useEffect(() => {
    const canvas = canvasRef.current
    const ctx = canvas.getContext('2d')
    ctx.clearRect(0, 0, window.innerWidth, window.innerHeight)

    var points = [[0,0],[window.innerWidth,0],[0,window.innerHeight],[window.innerWidth,window.innerHeight]];
    for (var items in locations){
        points.push( [locations[items].x, locations[items].y] );
    }
    var coordinates = getTriangleCoords(locations);

    for (var i=0;i<coordinates.length;i++) {
      var triangle = coordinates[i]
      var path=new Path2D();
      path.moveTo(triangle[0][0],triangle[0][1])
      path.lineTo(triangle[1][0],triangle[1][1])
      path.lineTo(triangle[2][0],triangle[2][1])
      ctx.stroke()
      ctx.fillStyle = "#eee"
      ctx.fill(path)
      ctx.stroke()
    }

    locations.forEach(location => draw(ctx, location))
  })

  return [locations, setLocations, canvasRef]
}
